        { img: "/images/index/select5.png", text: "12" },
        { img: "/images/index/select6.png", text: "500" },
      ],
      // 
      onscrolltop: false,
      scrolltop: 0,
    };
  },
  components: {
    showbox: showbox,
    showbox1: showbox1,
    scrolltop: scrolltop,
  },
  methods: {
    // 
    Scrolltop() {
      this.scrolltop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (this.scrolltop > 600) {
        this.onscrolltop = true;
      } else {
        this.onscrolltop = false;
      }
    },
    // 
    backtop() {
      let timer = setInterval(() => {
        let speed = Math.floor(-this.scrolltop / 5);
        document.documentElement.scrollTop = this.scrolltop + speed;
        if (this.scrolltop == 0) {
          clearInterval(timer);
        }
      }, 16);
    },
  },
  mounted() {
    window.addEventListener("scroll", this.Scrolltop);

  },
};
</script>
