  methods: {
    skip() {
      this.$router.push("/index");
    },
    // 
    Scrolltop() {
      this.scrolltop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (this.scrolltop > 600) {
        this.onscrolltop = true;
      } else {
        this.onscrolltop = false;
      }
    },
    // 
    backtop() {
      let timer = setInterval(() => {
        let speed = Math.floor(-this.scrolltop / 5);
        document.documentElement.scrollTop = this.scrolltop + speed;
        if (this.scrolltop == 0) {
          // this.onscrolltop = false;
          clearInterval(timer);
        }
      }, 10);
    },
    // 
    getdata() {
      let id = this.$route.query.id;
      axios({
        method: "get",
        url: "http://localhost:8080/text.json",
      })
        .then((res) => {
          let data = res.data.Lover.filter((item) => {
            return item.id == id;
          });
          this.detail = data[0];
          console.log(this.detail);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
  components: {
    scrolltop: scrolltop,
  },
  created() {
    console.log(this.$route.query.id);
    this.getdata();
  },
  mounted() {
    window.addEventListener("scroll", this.Scrolltop);
  },
};
