import Vue from "vue";
import VueRouter from "vue-router";
import index from "../views/index.vue";
import category from "../views/Category.vue";
import shop from "../views/shop.vue";
import my from "../views/my.vue";
import detailpage from "../views/detailpage.vue";
import login from "../views/login.vue";
import advertising from "../views/advertising.vue";


//1 
Vue.use(VueRouter);

const routes = [
  // {
  //   // 
  //   path: '/',
  //   redirect: '/index',
  //   meta: { //
  //     showTabbar: true
  //   }

  // },
  {
    // 
    path: "/advertising",
    name: "advertising",
    component: advertising,
    meta: { //
      showTabbar: false
    }
  },
  {
    // 
    path: "/index",
    name: "index",
    component: index,
    meta: { //
      showTabbar: true
    }

  },
  {
    // 
    path: "/index/detailpage/",
    name: "detailpage",
    component: detailpage,
    meta: { //
      showTabbar: false
    }
  },
  // 
  {
    path: "/category",
    name: "category",
    component: category,
    meta: { //
      showTabbar: true
    }

  },
  // 
  {
    path: "/shop",
    name: "shop",
    component: shop,
    meta: { //
      showTabbar: true
    }

  },
  // 
  {
    path: "/my",
    name: "my",
    component: my,
    meta: { //
      showTabbar: true
    }

  },
  {
    // 
    path: "/my/login",
    name: "login",
    component: login,
    meta: { //
      showTabbar: false
    }
  },
];

const router = new VueRouter({
  //html5    #
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return {
      x: 0,
      y: 0
    }
  }
})

// 
router.beforeEach((to, from, next) => {
  if (to.path == '/') {
    next('/advertising');

  } else {
    next();

  }
})
export default router;